import React, { useState, useEffect } from "react"

import GlobalStyle from "./GlobalStyle"
import api from './api'
import Header from "./components/Header"
import Conteiner from "./components/Conteiner"
import Footer from "./components/Footer"


function App() {
  const [dataDash, setDataDash] = useState({ active_calls: [], queue_agents: [], technicians_average: [], queue_stats: {}, overall_average: {}, msg: "", weather: "" })

  useEffect(() => {
    setInterval(() => {
      const loadAll = async function () {
        const requestApi = await api()
        if (dataDash != requestApi) {
          setDataDash(requestApi)
        }
      }
      loadAll()
    }, 5000)
  }, [])

  return (
    <div className="App">
      <GlobalStyle />

      <Header weather={dataDash.weather} />

      <Conteiner dataDash={dataDash} />

      <Footer dataDash={dataDash} />

    </div>
  )
}

export default App
